.app {
    position: relative;
    height: 50%;
    background: rgb(0, 0, 0);
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
    z-index: 20;
  }
  
  
  .swiper {
    width: auto;
    height: 18rem;
    margin-top: 2.7rem;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: rgb(255, 255, 255);
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 90%;
    object-fit: contain;
  }